// @import (less) "color.less";
.contenitor {
    display: table;
    border-collapse: separate;
    border-spacing: 0.875em 0em;
    margin: 0 auto;
}

.letter {
    text-align: center;
}

.enabled {
    background-color: transparent;
    font-size: 1.125em;
    font-family: Arial;
    font-weight: normal;
    margin: 1px;
    color: #329600;
    display: table-cell;
    vertical-align: middle;
    cursor:pointer;
    padding-top: 20px;
    padding-bottom: 20px;
}

// .enabled:hover {
//     color: #FFFFFF;
//     background-color: #258900;
// }

.searched {
    font-size: 1em;
    font-family: Arial;
    font-weight: bold;
    margin: 1px;
    color: #329600;
    background-color: transparent;
    display: table-cell;
    vertical-align: middle;
    cursor:pointer;
    padding-top: 20px;
    padding-bottom: 20px;
}

.selected {
    font-size: 1.125em;
    font-family: Arial;
    font-weight: bold;
    margin: 1px;
    color: #329600;
    background-color: transparent;
    display: table-cell;
    vertical-align: middle;
    cursor:pointer;
    padding-top: 20px;
    padding-bottom: 20px;
}

.disabled {
    background-color: transparent;
    font-size: 1.125em;
    font-family: Arial;
    font-weight: normal;
    margin: 1px;
    display: table-cell;
    vertical-align: middle;
    pointer-events: none;
    opacity: 0.5;
    color: #CCCCCC;
    cursor:none;
    padding-top: 20px;
    padding-bottom: 20px;
}



